import * as React from 'react';
import { graphql } from 'gatsby';
import { Layout } from '~src/context/Layout';
import { Breadcrumb } from '~src/components/Breadcrumb';
//const breadCrumbs = [{ title: 'About', href: '/about' }];

const PagesTemplate = ({ data }) => {
  const seo = {
    title: data.wpPage.title,
    article: true,
    image: data.wpPage?.featuredImage?.node?.localFile?.childImageSharp?.gatsbyImageData?.images?.fallback?.src,
  };
  return (
    <Layout seo={seo}>
      <section className="uk-section-muted uk-section-small">
        <div className="uk-container">
          <div className="uk-grid-medium uk-child-width-1-1" data-uk-grid>
            <Breadcrumb currentPage={data.wpPage.title} />
            <section>
              <div>
                <article className="uk-card uk-card-default uk-card-body uk-artictle">
                  <header className="uk-text-center">
                    <h1 className="uk-article-title">{data.wpPage.title}</h1>
                  </header>
                  <div className="uk-article-body">
                    <div dangerouslySetInnerHTML={{ __html: data.wpPage.content }}></div>
                  </div>
                </article>
              </div>
            </section>
          </div>
        </div>
      </section>
    </Layout>
  );
};

export default PagesTemplate;
export const query = graphql`
  query PageTemplateQuery($id: String) {
    wpPage(id: { eq: $id }) {
      id
      title
      content
      featuredImage {
        node {
          title
          localFile {
            childImageSharp {
              gatsbyImageData(layout: FULL_WIDTH)
            }
          }
        }
      }
    }
  }
`;
